@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: 'Noto Sans JP', sans-serif;
$second-font-family: 'Noto Serif', serif;
$third-font-family: 'Lato', sans-serif;
$forth-font-family: 'Gotu', sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 300;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black1: #000;
$black: #0D2936;
$white: #fff;
$green: #408F95;
$green2: #DDF0F1;
$red: #C94800;

a:hover{
  opacity: .65;
  transition: all .4s;
}

body.is-open{
  overflow: hidden;
}

body{
  color: $black;
}