  opacity: 0; /* デフォルトのボタンを非表示 */
  position: absolute;
}

/* チェック後のボタン */
input[type=radio]:checked + .wpcf7-list-item-label::before {
  background: url("../../assets/images/common/check-red.png") no-repeat center;
  background-size: contain;
  content: "";
  height: 0.875rem;
  left: 0;
  position: absolute;
  top: 0.21875rem;
