.form__submit:hover {
  background: #fff;
  color: #408F95;
  border-color: #408F95; /* hover時に境界線の色を変更 */
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  opacity: 1;
}

.form__submit:before {
  content: "";
  position: absolute;
  border: 1px solid #408F95;
  height: calc(100% - 2px);
  top: 0.375rem;
  left: 0.375rem;
  width: calc(100% - 2px);
  z-index: 5;
}

.form__submit:after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: 5;
}

.form__submit:hover:after {
  transform: scale(1, 1);
}

.form__submit span {
  text-align: center;
  position: relative;
  z-index: 10;
}

.form__submit:after {
  content: "";
  display: inline-block;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  width: 2.5rem;
  height: 0.375rem;
  margin-left: 0.4375rem;
  margin-bottom: 0.125rem;
  transform: skewX(45deg);
  z-index: 10;
}

.form__submit:hover span:after {
